<template>
    <div class="detail">
        <van-nav-bar
        title="商品详情"
        left-text="返回"
        left-arrow
        @click-left="black"
        />
        <div class="detail-box">
            <div class="detail-img">
                <img :src="productDetails.large_img">
            </div>
            <div class="detail-con">
                <div class="padding">
                    <div v-for="(item,index) in productDetails.rules" 
                    :key="index">
                        <div class="con-box" v-if="!item.rule[0].title == ''">
                            <div class="con-title">{{item.title}}</div>
                            <div :class="{con : item.ruleIndex == i}" v-for="(v,i) in item.rule" :key="i" @click="selectRules(item,i)">{{v.title}}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="detail-desc-box">
                <div class="padding">
                    <div class="detail-desc-title">商品描述</div>
                    <div class="detail-desc" v-for="(item,index) in productDetails.desc" :key="index">
                        {{index+1}}.{{item}}
                    </div>
                </div>
            </div>
        </div>

        <van-nav-bar>
             <template #left>
               <div class="price">￥{{productDetails.price}}</div>
            </template>
            <template #right>
               <van-stepper v-model="count" theme="round" button-size="20" @change="addCount"/>
            </template>
        </van-nav-bar>

        <van-goods-action>
            <van-goods-action-icon icon="cart-o" text="购物车"   
            :badge="shopCarCount==0 ? '' 
            : shopCarCount" :color="shopCarCount > 0 ? '#de3f2a' : '#646566'" @click="goShopcar"/>
            <van-goods-action-icon icon="like" text="收藏"  :color="isColl ? '#de3f2a' : '#646566'" @click="collection"/>
            <van-goods-action-button color="#fa5944" type="warning" text="加入购物车"  @click="addCar"/>
            <van-goods-action-button color="#de3f2a" type="danger" text="立即购买" @click="buy"/>
        </van-goods-action>

    </div>
</template>

<script>

    import '../assets/less/detail.less'
    export default {
        name: 'Detail',
        data() {
            return {
                // 商品pid
                pid:'',
                // 添加商品数量
                count: 1,
                isCold: true,
                isHot:true,
                // 商品详情数据
                productDetails: '',
                shopCarCount: 0,
                isColl: false,
                sid: ''
            }
        },
        created(){
            this.pid = this.$route.params.pid;
            //获取商品信息
            this.getProductDetail();
            this.checkShopCarCount();
            //查询收藏商品
            this.getLike();
        },
        methods: {
            black(){
                this.$router.go(-1);
            },
            //添加商品数量
            addCount(value){
                this.count = value;
            },
            //选择商品规格
            selectRules(item,i){
                if(item.ruleIndex == i){
                    return;
                }
                item.ruleIndex = i;
            },

            //获取商品数据
            getProductDetail(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "GET",
                    url: "/productDetail",
                    params:{
                        appkey: this.appkey,
                        pid: this.pid
                    }
                }).then(res => {
                    

                    this.$toast.clear();
                    if(res.data.code == 600){
                        this.detailImg = res.data.result[0].large_img;
                        this.desc = res.data.result[0].desc;
                        let data = res.data.result[0];
                        data.desc = data.desc.split(/\n/);
                        let rules = [];
                        let ruleData = ['tem', 'sugar','milk','cream'];
                        ruleData.map(v => {
                            let r = {};
                            r.title = data[v + '_desc'];
                            r.ruleIndex = 0;
                            r.rule = [];
                            let ruleString = data[v].split('/');
                            ruleString.map(value => {
                                r.rule.push({title: value});
                            })
                            if(r.rule.length > 0){
                                rules.push(r);
                            }
                        })
                        data.rules = rules;
                        this.productDetails = data;
                    }
                })
            },   
            // 添加购物车   
            addCar(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast("请先登录");
                    return this.$router.push({name:'Login'});
                }
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })
                let rs = [];
                let rule = [];
                this.productDetails.rules.map(v => {
                    rs.push(v.rule[v.ruleIndex].title);
                })
                rs.map(v => {
                    if(v !== ''){
                        rule.push(v);
                    }
                })
                let ruleV = rule.join('/');
                this.axios({
                    method: 'POST',
                    url: '/addShopcart',
                    data: {
                        pid: this.pid,
                        count: this.count,
                        rule: ruleV,
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    if(res.data.code == 700){
                        //token检验无效，则跳到登录页面
                        this.$toast.push({name: 'Login'});
                    }else if(res.data.code == 3000){
                        
                        this.sid = res.data.sid;
                        this.$toast.clear();
                        this.checkShopCarCount();
                    }
                    this.$toast(res.data.msg);
                })
            },
            //查询购物车商品总数量接口
            checkShopCarCount(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    return;
                }
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })
                this.axios({
                    method: "GET",
                    url: '/shopcartCount',
                    params:{
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 4000){
                        this.shopCarCount = res.data.result;
                    }
                })
            },
            //收藏
            collection(){
                let tokenString = localStorage.getItem('token');
                let url = this.isColl ? '/notlike' : '/like';
                this.axios({
                    method: 'POST',
                    url,
                    data:{
                        appkey: this.appkey,
                        pid: this.pid,
                        tokenString,
                    }
                }).then(res => {
                    if(res.data.code == 800){
                        this.isColl = true;
                    }else if(res.data.code == 900){
                        this.isColl = false;
                    }
                }).catch(err => {
                    
                })
            },
            goShopcar(){
                this.$router.push({name: 'ShopCar'});
            },
            getLike(){
                let tokenString = localStorage.getItem('token');
                this.axios({
                    method: 'GET',
                    url:'findlike',
                    params:{
                        appkey: this.appkey,
                        pid: this.pid,
                        tokenString,
                    }
                }).then(res => {
                    if(res.data.code == 1000 && res.data.result.length !== 0){
                        if(res.data.result[0].pid == this.pid){
                            this.isColl = true;
                        }
                    }
                }) 
            },
            buy(){
                this.addCar();
                setTimeout(() => {                    
                    this.$router.push({name: 'Order',query:{sids:this.sid}})
                },500)
            }
        },
    }
</script>